* {
  font-family: "Varela Round", sans-serif;
  font-weight: bold;
}
.movie {
  display: block;
}
input {
  outline: none;
  border: 2px solid black;
  border-radius: 10px;
  margin-right: 0px;
  padding: 10px;
}
.icon {
  margin-right: 20px;
  cursor: pointer;
}
#b1 {
  outline: none;
  cursor: pointer;
  border-radius: 10px;
  padding: 10px;
  background-color: yellow;
  color: red;
  margin-top: 10px;
}
td {
  border: 2px solid black;
  font-size: 20px;
}
table {
  margin: 50px auto;
  display: block;
}
td {
  padding: 20px;
}
